@font-face {
  font-family: custom;
  src: url('../../../assets/fonts/bukhari_script.ttf');
}

@font-face {
  font-family: bakerie_bold;
  src: url('../../../assets/fonts/bakerie_bold.otf');
}

body {
  font-family: bakerie_bold;
  margin: 0px;
  background: linear-gradient(to left, slategrey, beige);
  word-spacing: 5px;
  user-select: none;
}

.special {
  color: #0066a4;
}

.banner {
  position: fixed;
  left: 0;
  z-index: 1;
  font-family: custom !important;

  >ul {
    list-style: none;
    padding: 0;
    padding-top: 50px;

    li {
      margin: 15px 0;
      height: 30px;
      width: max-content;
      padding: 5px 15px;
      border-radius: 20px;
      transition: 200ms;
      display: inline-flex;
      align-items: center;
      justify-content: flex-start;
      color: white;

      &:hover {
        background-color: #2775a5;
        box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.2);
        transition: 200ms;
      }

      >img {
        width: 20px;
        height: 20px;
        margin-right: 10px;
      }
    }
  }
}

.contact {
  .content {
    display: grid;
    justify-items: center;
  }

  .links {
    display: flex;
    gap: 20px;

    img {
      width: 50px;
      height: auto;
      padding: 8px;
      border-radius: 5px;
      background-color: white;
      transition: 300ms;

      &:hover {
        box-shadow: 1px 2px 5px 0px grey;
        transition: 300ms;
      }
    }
  }
}

.before-heading {
  font-size: 15px;
  margin: 10px 0px;

  &::before {
    content: '';
    display: inline-flex;
    vertical-align: middle;
    width: 60px;
    height: 1px;
    background-color: black;
  }
}

.heading {
  font-size: 30px;
  margin: 10px 0px;
}

.content {
  margin-top: 40px;
  padding: 40px 40px;
  background-color: white;
  border-radius: 20px;
  font-size: 20px;
  box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
}

.content p {
  line-height: 40px;
}

.tools {
  margin-top: 40px;
}

.skill-logos,
.tools-logos {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.tools-logos div:last-child {
  font-size: 15px;
}

.project {
  text-decoration: none;
  color: black;
  margin-bottom: 50px;
  padding: 20px 10px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid rgb(220, 220, 220);
  border-radius: 20px;
  box-shadow: 5px 5px 10px 0px rgb(0, 0, 0, 0.1);
  cursor: pointer;

  &:last-child {
    margin-bottom: 10px !important;
  }

  .project-content {
    text-align: justify;

    >p {
      margin: 0;
    }
  }

  .line-break {
    display: none;
  }

  >img {
    width: 400px;
    border-radius: 10px;
  }
}

.project-content a {
  padding: 5px 10px;
  color: #0066a4;
  text-decoration: underline;
}

div:has(> .form) {
  display: flex;
  justify-content: space-around;
}

.form {
  display: grid;

  >input[type='text'] {
    width: 300px;
    height: 20px;
    padding: 10px 20px;
    vertical-align: middle;
    margin-bottom: 20px;
    border-radius: 20px;
    border: 1px solid grey;
    outline: none;

    &:focus {
      border: 2px solid #0066a4;
    }
  }
}

.form-heading {
  margin: 10px;
}

.form-buttons input {
  padding: 10px 20px;
  color: white;
  background-color: #0066a4;
  border: 0;
  border-radius: 20px;
  outline: none;
  transition: 200ms;

  &:nth-child(1):hover {
    box-shadow: 1px 1px 4px 0px green;
    transition: 200ms;
  }

  &:nth-child(2):hover {
    box-shadow: 1px 1px 4px 0px red;
    transition: 200ms;
  }
}

.contact-detail {
  display: grid;
  align-content: center;

  >div {
    display: grid;
    margin-bottom: 20px;

    >span {
      margin: 0;
    }
  }
}

.detail-heading {
  font-size: 15px;
  color: #0066a4;
}

.home-footer {
  height: 50px;
  background: linear-gradient(to left, #0066a4 0%, #00375a 100%);
  text-align: center;
  display: grid;
  align-items: center;
  color: white;
  font-size: 20px;
}

@media (max-width: 576px) {
  .container {
    padding: 20px;

    .home {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .photo {
        width: 200px;
      }

      .home-content {
        margin-top: 20px;
        text-align: center;
      }
    }
  }
}

@media (min-width: 576px) {
  .home {
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    .photo {
      width: 300px;
    }
  }
}

@media (min-width: 768px) {
  .container {
    padding-left: 25px;
  }
}

@media (min-width: 992px) {
  .banner {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 65px;
    height: 100vh;
    background: linear-gradient(#0066a4 0%, #00375a 100%);
    text-align: center;
    transition: 300ms;

    .logo {
      width: 100%;
      height: 62px;
      padding-top: 10px;
      color: white;
      font-size: 30px;
      text-decoration: none;
      background-color: #00375a;
      border-bottom: 2px solid white;
      margin-bottom: 60px;
    }

    .hidden,
    .nav-name,
    .menu-icon {
      display: none;
    }

    &:hover {
      width: 120px;
      transition: 300ms;

      .hidden,
      .nav-name {
        display: contents;
        margin: 0;
        padding: 0;
      }

      .nav-name {
        margin-left: 10px;
      }

      .logo {
        height: 110px;
        margin-bottom: 12px;
      }
    }
  }

  .container {
    padding-left: 125px;

    .work {
      img {
        padding-right: 20px;
      }
    }
  }
}

.container {
  .home,
  .about,
  .skills,
  .work,
  .contact {
    min-height: calc(100vh - 100px);
    padding-top: 100px;
  }


  /* CSS FOR HOME SECTION */
  .home {
    .photo {
      border-radius: 200px;
      box-shadow: 5px 5px 10px 0px #0066a4;
      margin-right: 40px;
    }

    h4 {
      text-align: justify;
    }
  }

  .box {
    min-width: 80px;
    flex: 1;
    height: 80px;
    padding: 5px;
    display: grid;
    align-content: center;
    justify-content: center;
    justify-items: center;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
    transition: 300ms;

    &:hover {
      box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.2);
      transition: 300ms;
    }

    >span {
      width: fit-content;
    }

    >img {
      width: 30px;
      height: auto;
    }
  }
}

@media (max-width: 991.9px) {
  .banner {
    width: 100vw;
    background: linear-gradient(to left, #0066a4, #00375a);
    box-shadow: 1px 0px 4px 0 rgba(0, 0, 0, 0.2);

    .logo {
      float: left;
      font-size: 30px;
      padding: 10px 20px;
    }

    a {
      color: white;
      text-decoration: none;
    }

    .darkmode-parent {
      position: relative;
      top: 18px;
    }

    .menu-icon {
      position: absolute;
      top: 20px;
      right: 50px;
      width: 20px;
      height: 20px;
      cursor: pointer;
      z-index: 99;

      .navicon {
        width: 18px;
        height: 2px;
        display: block;
        position: relative;
        margin-top: 10px;
        background-color: white;
        transition: background 0.2s ease-out;

        &::before {
          top: 5px;
        }

        &::after {
          top: -5px;
        }

        &::before,
        &::after {
          background-color: white;
          content: '';
          display: block;
          height: 100%;
          position: absolute;
          transition: all 0.2s ease-out;
          width: 100%;
        }
      }
    }

    .menu {
      display: grid;
      margin: 0;
      max-height: 0;
      padding: 0 20px 0;
      overflow: hidden;
      clear: both;
      transition: max-height 200ms ease-out;

      li {
        margin: 10px 0;
      }
    }

    .menu-button:checked~.menu {
      max-height: 275px;
    }

    .menu-button:checked~.menu-icon .navicon {
      background-color: transparent;
    }

    .menu-button:checked~.menu-icon .navicon:before {
      transform: rotate(-45deg);
    }

    .menu-button:checked~.menu-icon .navicon:after {
      transform: rotate(45deg);
    }

    .menu-button:checked~.menu-icon:not(.steps) .navicon:before,
    .menu-button:checked~.menu-icon:not(.steps) .navicon:after {
      top: 0;
    }
  }

  .home-content p,
  .content p {
    text-align: justify;
  }

  .buttons {
    width: auto;
    justify-content: space-evenly;
  }

  .title {
    text-align: center;
  }

  .project {
    display: flex;
    flex-direction: column;
  }

  .project img {
    width: 90%;
  }

  .project-content h3,
  .project-content h5 {
    display: flex;
    justify-content: center;
    gap: 10px;
  }

  .form-content {
    display: block;
    width: 90%;
    margin: auto;
  }

  .contact-detail {
    margin: 40px 0px 0px !important;
  }

  .contact-detail div {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .content {
    padding: 20px;
  }

  .form input[type='text'] {
    width: auto;
  }

  footer {
    margin-top: 40px;
  }
}

.dark {
  background: #1f1f1f;

  .home,
  .before-heading,
  .project,
  .heading,
  .content {
    color: white;
  }

  .before-heading::before {
    background-color: white;
  }

  .content,
  .box,
  .form-field {
    background-color: black;
    box-shadow: 3px 3px 10px 0px rgba(255, 255, 255, 0.6);
  }
}

/* DARKMODE  */

.darkmode-parent .sun {
  padding: 5px;
  border-radius: 20px;
  background: linear-gradient(180deg, #ffcc89, #d8860b);
}

.darkmode-parent .moon {
  padding: 5px;
  border-radius: 20px;
  background: linear-gradient(180deg, #fff, #3a3a3a);
}

body::-webkit-scrollbar {
  width: 12px;
  height: 12px;
  background-color: transparent;
}

body::-webkit-scrollbar-thumb {
  background-color: #b0b0b0;
  border-radius: 3px;
}